import { useEffect, useState } from "react";
import Filter from "./components/Filter";
import Formperson from "./components/FormPerson";
import Message from "./components/Message";
import PhoneList from "./components/PhoneList";
import noteSevice from "./services/Note";

const App = () => {
  const [persons, setPersons] = useState([""]);
  const [filterPerson, setFilterPerson] = useState("");
  const [message, setMessage] = useState("");

  useEffect(() => {
    noteSevice.getAllPersons().then((res) => {
      setPersons(res);
    });
  }, [persons]);

  useEffect(() => {
    setTimeout(() => {
      setMessage("");
    }, 5000);
  }, [message]);

  const handleOnChange = (event) => {
    setFilterPerson(event.target.value);
  };

  const handleSubmit = (values) => {
    const personToAdd = {
      name: values.name,
      number: values.number,
    };

    const handleUpdate = (personToAdd) => {
      const persona = persons.find((p) => p.name === personToAdd.name);
      const changedPerson = { ...persona, number: personToAdd.number };

      noteSevice
        .updateNumber(changedPerson.id, changedPerson)
        .then((response) => {
          setPersons(
            persons.map((person) =>
              person.name !== response.name ? person : response
            )
          );
          setMessage("updated");
        })
        .catch(() => {
          setMessage(`${personToAdd.name} doesn't exist in server`);
        });
    };

    if (personToAdd.name === "" || personToAdd.number === "") {
      alert("Both field are required");
    } else {
      if (persons.some((person) => person.name === personToAdd.name)) {
        if (
          window.confirm(
            `${personToAdd.name} already exists in the list, want to replace her number?`
          )
        ) {
          handleUpdate(personToAdd);
        } else {
          alert("not updated");
        }
      } else {
        noteSevice
          .addPerson(personToAdd)
          .then((returnedPerson) => setPersons(persons.concat(returnedPerson)));
        setMessage("added");
      }
    }
    values.name = "";
    values.number = "";
  };

  const handleDelete = (person) => {
    if (window.confirm(`Are u sure? ${person.name} will be deleted`) === true) {
      const copy = [...persons];

      const updateList = () => {
        const replaceList = copy.filter((per) => per.id !== person.id);
        setPersons(replaceList);
        setMessage("deleted");
      };
      noteSevice
        .deletePerson(person.id)
        .then(() => updateList())
        .catch(() => {
          setMessage(`${person.name} doesn't exist in server`);
        });
    }
  };
  return (
    <div>
      <h1>PhoneBook App</h1>
      {message === "" ? null : message === "deleted" ? (
        <Message color={"red"} message={message} />
      ) : message === "added" ? (
        <Message color={"green"} message={message} />
      ) : message === "updated" ? (
        <Message color={"yellow"} message={message} />
      ) : (
        <Message color={"red"} message={message} />
      )}
      <Message message={message.status} color={message.color} />
      <Filter filtered={filterPerson} handleOnChange={handleOnChange} />
      <br />
      <Formperson submit={handleSubmit} />
      <PhoneList
        persons={persons}
        filtered={filterPerson}
        handleDelete={handleDelete}
      />
    </div>
  );
};

export default App;
