import Person from "./Person";

const PhoneList = ({ persons, filtered, handleDelete }) => {
  return (
    <ul>
      {filtered === ""
        ? persons.map((person, i) => (
            <Person key={i} person={person} i={i} handleDelete={handleDelete} />
          ))
        : persons.map((person, i) =>
            person.name.toLowerCase().includes(filtered.toLowerCase()) ? (
              <Person
                key={i}
                person={person}
                i={i}
                handleDelete={handleDelete}
              />
            ) : null
          )}
    </ul>
  );
};

export default PhoneList;
