import axios from "axios";

const baseUrl = "https://rugged-saguaro-96158.herokuapp.com/api/persons";

const getAllPersons = () => {
  const request = axios.get(baseUrl);
  return request.then((response) => response.data);
};

const addPerson = (personToAdd) => {
  const request = axios.post(baseUrl, personToAdd);
  return request.then((response) => response.data);
};

const deletePerson = (id) => {
  const request = axios.delete(`${baseUrl}/${id}`);
  return request.then((response) => response.data);
};

const updateNumber = (id, newObject) => {
  const request = axios.put(`${baseUrl}/${id}`, newObject);
  return request.then((response) => response.data);
};

export default { getAllPersons, addPerson, deletePerson, updateNumber };
